// ** React Imports
import { Link } from 'react-router-dom'

// ** Custom Components
import Avatar from '@components/avatar'
import { Can } from '@src/utility/context/Can.js'
// ** Store & Actions
import { store } from '@store/store'
import { getDsc, deleteDigitalSignaturelist } from '../store'

// ** Icons Imports
import { MoreVertical, Trash2, Eye, Edit, CheckCircle, XCircle } from 'react-feather'

// ** Reactstrap Imports
import { Badge, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, Col } from 'reactstrap'
import moment from 'moment'

import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

// ** Renders Client Columns

const locationArr = ['', 'Office', 'With Client']
const renderClient = row => {

  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.name || ''}
    />
  )

}

const MySwal = withReactContent(Swal)

const deletefun = (id) => {

  return MySwal.fire({
    title: 'Are you sure?',
    text: "You won't be able to revert this!",
    icon: 'warning',
    showCancelButton: true,
    confirmButtonText: 'Yes, delete it!',
    customClass: {
      confirmButton: 'btn btn-primary',
      cancelButton: 'btn btn-outline-danger ms-1'
    },
    buttonsStyling: false
  }).then(async (result) => {
    if (result.value) {
      await store.dispatch(deleteDigitalSignaturelist(id))
      MySwal.fire({
        icon: 'success',
        title: 'Deleted!',
        text: 'Digital Signature has been deleted.',
        customClass: {
          confirmButton: 'btn btn-success'
        }
      })
    }
  })
}


const statusObj = [
  '',
  'light-primary',
  'light-success',
  'light-danger'
]

const statusArr = [
  '',
  "Active",
  "Inactive",
  "Expired"
]

const dateFormat = (value) => {
  if (value.length === 13) {
    return moment(value, 'x').format("MMM DD, YYYY")
  } else {
    return moment.unix(value).format("MMM DD, YYYY")
  }

}

export const columns = [
  {
    name: 'ID',
    sortable: true,
    minWidth: '172px',
    sortField: 'role',
    selector: row => row.id,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        <Link
          to={`/digital-signature/view/${row.id}`}
          className='user_name text-truncate text-body'
          onClick={() => store.dispatch(getDsc(row.id))}
        >
          <span className='text-capitalize fw-bolder'>{row.uniqueidentity}</span>
        </Link>
      </div>
    )
  },
  {
    name: 'Client',
    sortable: true,
    minWidth: '300px',
    sortField: 'client',
    selector: row => row.name,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        {renderClient(row)}
        <div className='d-flex flex-column'>
          <span className='fw-bolder'>{row.name}</span>
          <small className='text-truncate mb-0'>{row.contactname}</small>
        </div>
      </div>
    )
  },
  {
    name: 'Issued Date',
    sortable: true,
    minWidth: '172px',
    sortField: 'role',
    selector: row => row.issueddate,
    cell: row => <span className='text-capitalize'>{dateFormat(row.issueddate)}</span>
  },
  {
    name: 'Expiry Date',
    minWidth: '138px',
    sortable: true,
    sortField: 'currentPlan',
    selector: row => row.expirydate,
    cell: row => <span className='text-capitalize'>{dateFormat(row.expirydate)}</span>
  },
  {
    name: 'Location',
    minWidth: '138px',
    sortable: true,
    sortField: 'currentPlan',
    selector: row => row.location,
    cell: row => { return (row.dsclocation ? <Badge className='text-capitalize' color={statusObj[row.dsclocation]} pill>{locationArr[row.dsclocation]}</Badge> : '') }
  },
  {
    name: 'Status',
    minWidth: '138px',
    sortable: true,
    sortField: 'status',
    selector: row => row.status,
    cell: row => (
      <Badge className='text-capitalize' color={statusObj[row.status]} pill>
        {statusArr[row.status]}
      </Badge>
    )
  },
  {
    name: 'Actions',
    minWidth: '100px',
    cell: row => (
      <div className='column-action d-flex align-items-center'>
        <Can I={'read'} a={'digital-signature'}>
          <Col tag={Link} lg={4}
            to={`/digital-signature/view/${row.id}`}
            onClick={() => store.dispatch(getDsc(row.id))}>
            <Eye
              className='cursor-pointer mt-0' size={16} />
          </Col>
        </Can>
        <Can I={'write'} a={'digital-signature'}>
          <Col tag={Link} lg={4}
            to={`/digital-signature/edit/${row.clientid}`}>
            <Edit
              className='cursor-pointer ms-1 mt-0' size={16} />
          </Col>
        </Can>
        <UncontrolledDropdown>
          <DropdownToggle tag='div' className='btn btn-sm'>
            <MoreVertical size={14} className='cursor-pointer' />
          </DropdownToggle>
          <DropdownMenu>
            <Can I={'delete'} a={'digital-signature'}>
              <DropdownItem
                tag='a'
                href='/'
                className='w-100'
                onClick={e => {
                  e.preventDefault()
                  deletefun(row.id)
                }}
              >
                <Trash2 size={14} className='me-50' />
                <span className='align-middle'>Delete</span>
              </DropdownItem>
            </Can>
          </DropdownMenu>
        </UncontrolledDropdown>
      </div>
    )
  }
]
